
.duo {
    display: flex;
    flex-direction: column;
    gap: 31px;



    &__area {
        position: relative;

        width: 206px;
        min-height: 206px;

        display: flex;
        justify-content: center;
        align-items: center;

        border-radius: 7px;

        color:  #535355;

        p {
            text-align: center;
        }
    }

    &__atom {
        position: absolute;

        background-color: #706f72;
        opacity: 0.1;

        $size-atom: 60%;

        &_transparent {
            opacity: 0;
        }

        &_top {
            inset: 0px auto auto auto;
            width: $size-atom;
            height: 1px;
        }
        &_right {
            inset: auto 0px auto auto;
            width: 1px;
            height: $size-atom;
        }
        &_bottom {
            inset: auto auto 0px auto;
            width: $size-atom;
            height: 1px;
        }
        &_left {
            inset: auto auto auto 0px;
            width: 1px;
            height: $size-atom;
        }
    }

    &__btns {
        display: flex;
        justify-content: space-between;

        padding-inline: 7px;
    }
}