
.field {
    display: flex;
    flex-direction: column;
    
    & p {
        margin: 0;
        padding: 0;
    }
}

.input {
    padding: 10px 20px;
    border: none;

    font-family: inherit;
    font-size: 15px;

    &:focus {
        border: none;
        outline: none;
    }

    &__container {
        position: relative;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            height: 1px;
            width: 100%;
            background-color: #706f72;
        }

    }

    
}