
.button {
    padding-inline: 15px;
    
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    height: 35px;

    border-radius: 7px;
    border: 1px solid var(--color-orange);

    color: var(--color-orange);
    font-size: var(--font-size-15);

    background-color: white;

    opacity: 0.7;

    &:disabled {
        opacity: 0.3;
    }
}